import moment from 'moment';
import { useState } from 'react';

export const MonthView = ({ firstDay, endDay }) => {
  const [dateRange, setDateRange] = useState(moment.range(firstDay, endDay));

  return (
    <div className="conatiner-fluid">
      {console.log(moment().startOf('month').weekday())}
      <nav aria-label={moment().year()}>
        <ul className="pagination">
          {moment.months().map((month, index) => (
            <li className="page-item" key={month} id={index + 1}>
              <a className="page-link">{month}</a>
            </li>
          ))}
        </ul>
      </nav>
      <hr className="month-line"></hr>
      <table className="table">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => (
              <th key={weekDay} className="days-of-week">
                {weekDay}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          
        </tbody>
      </table>
    </div>
  );
};
